.Login {
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    font-family: Quicksand;
}

.Login .Login-Content {
    width: 0;
    min-height: 50vh;
    background-color: #2474c1;
    animation: fullWidth 1s linear 1 forwards alternate;
    padding: 30px 50px;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.Login .Login-Content .LogoDiv {
    width: 300px;
    height: 200px;
    position: absolute;
    top: calc( 25vh - 100px );
    right: calc( 35% - 150px );
    display: none;
}

.Login .Login-Content .Login-Welcome-Content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: calc( 50vh - 60px );
    width: 70%;
}

.Login .Login-Content .WelcomeDiv {
    color: #fff;
    width: 0;
    overflow: hidden;
    animation: fullWidth 1s 1.5s linear 1 forwards alternate;
}

.Login .Login-Content .WelcomeDiv h4,
.Login .Login-Content .WelcomeDiv p {
    white-space: nowrap;
}

.Login .Login-Content .LoginForm {
    width: 40%;
    height: 70vh;
    padding: 10vh 20px;
    background-color: #fff;
    position: absolute;
    top: -10vh;
    right: 10%;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    color: #2474c1;
    text-align: center;
    overflow: hidden;
    transform: scale(0.8);
    opacity: 0;
    animation: popup .2s linear 2.5s 1 forwards alternate;
}

.Login .Login-Content .LoginForm .circle {
    border-radius: 50%;
    background-color: #2474c1;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    width: 70px;
    height: 70px;
    margin: 30px auto;
}

.Login .Login-Content .LoginForm .inputs {
    width: 100%;
    border: 0;
    border-bottom: 1px solid lightgray;
    font-size: 12px;
    padding: 10px;
    margin-bottom: 15px;
}

.Login .Login-Content .LoginForm .inputs:focus {
    outline: none;
}

.Login .Login-Content .LoginForm .btns {
    background-color: #2474c1;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    color: #fff;
    width: 100%;
    border: 0;
    padding: 8px;
    margin-bottom: 15px;
}

.Login .Login-Content .LoginForm .btns:focus {
    outline: none;
}

@media screen and (max-width: 1200px) {
    .Login .Login-Content {
        animation: fullWidth1200 1s linear 1 forwards alternate;
    }
}
 
@media screen and (max-width: 992px) {
    .Login .Login-Content {
        animation: fullWidth992 1s linear 1 forwards alternate;
    }
}

@media screen and (max-width: 768px) {
    .Login .Login-Content {
        min-height: 50vh !important;
    }
    .Login .Login-Content .LoginForm {
        width: 90% !important;
        /* height: 60vh; */
        padding: 20px;
        /* top: 15vh; */
        right: 5% !important;
    }
    .Login .Login-Content .Login-Welcome-Content {
        height: 90vh;
        width: 100%;
        position: fixed;
        top: 5vh;
    }
    
    .Login .Login-Content .WelcomeDiv {
        color: #2474c1 !important;
        animation: fullWidth768 1s 1.5s linear 1 forwards alternate;
    }
}

@media screen and (max-width: 600px) {
    .Login .Login-Content {
        padding: unset 0;
    }
    .Login .Login-Content .LoginForm {
        width: 80%;
        right: 10%;
    }
}

.Login .Login-ContentBack {
    animation: fullWidthBackword .3s .3s linear 1 forwards alternate;
    width: 70%;
}

.Login .Login-Content .WelcomeDivBack {
    animation: fullWidthBackword .2s linear 1 forwards alternate;
    color: #fff;
    width: 70%;
}

@keyframes fullWidthBackword {
    from {
        width: 70%;
        opacity: 1;
    }
    to {
        width: 0;
        opacity: 0;
    }
}

.Login .Login-Content .LoginFormBack {
    animation: popupBackword .2s linear 1 forwards alternate;
}
@keyframes popupBackword {
    from {
        opacity: 1;
        transform: scale(1);
    }
    50%  {
        opacity: 1;
        transform: scale(1.1);
    } /* ignored */
    to {
        opacity: 0;
        transform: scale(0.8);
    }
}